import {mapState} from 'vuex'
export default {
  props:{
    content:{
      type:String,
      default:"文章详情",
      require:true
    },
    canBack:{
      type:Boolean,
      default:true
    },
    shareUrl:{
      type:String,
      default:null
    },
    backFunc:{
      type:Function,
      default:null
    }
  },
  methods:{
    goBack(){
      if(this.$props.backFunc == null){
          this.$router.go(-1);
      }
      else{
        this.$props.backFunc();
      }
    },
    goShare(){
      if(this.shareUrl){
        window.open(this.shareUrl);
      }
    }
  },
  computed:{
    ...mapState({
      scrollerTop:state => state.scrollerTop
    })
  },
  watch:{
    scrollerTop(){
      let _top = this.scrollerTop;
      let _dom = document.getElementById("title");
      if(_top >= 180 && _top <= 420){
        let _opacity = _top/420;
        _dom.style.background = '#222';
        _dom.style.opacity = _opacity;
      }
      else if(_top < 180){
        _dom.style.background = "linear-gradient(0deg, rgba(0,0,0,0.00) 0%, rgba(0,0,0,0.6) 95%)";
        _dom.style.opacity = 1;
      }
    }
  }

}



