
import api from "./../api/index"
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
;
export default {
  data(){
    return {
      isError:false,
      isLoading:false,
      requestData:{id:null},
      themeData:null
    }
  },
  methods:{
    _getThemeById(){
      let _this = this;
      this.isLoading = true;
      api.getTopicsById(this.$route.query.id).then(function(data){
        _this.isLoading = false;
        _this.themeData = data.data;
        _this.isError = false;
      },function(){
        _this.isLoading = false;
        _this.isError = true;
      })
    },
    onClick(id) {
      this.$router.push({
        path: 'con',
        query: {
          id: id || ""
        }
      });
    }
  },
  watch:{
      '$route'(to,from){
        if(to.query.id != from.query.id){
            this._getThemeById(to.query.id);
        }
      }
  },
  components:{
    Cell,
    Group
  },
  mounted(){
    this.requestData.id = this.$route.query.id;
    this._getThemeById(this.$route.query.id);
    let _this = this;
    // window.onscroll = function(){
    //   let _top = document.documentElement.scrollTop || document.body.scrollTop;
    //   _this.$store.commit("changeScrollTop",{
    //     _top:_top
    //   })
    // }
  }
}



