import api from './../api/index';
export default {
  data() {
    return {
      newsContent: null,
      requestData: {
        id: null
      },
      isLoading: false,
      isError: false,
      linkcss: null,
      titleImg: null,
      imgArr:[],
      showBg:false
    }
  },
  methods: {
    getNewsById(id) {
      this.isLoading = true;
      this.isError = false;
      let _this = this;
      api.getNewsById(id).then(function(data) {
        _this.newsContent = data.data;
        _this.titleImg = data.data.image || null;
        if (_this.newsContent.css.length != 0 && _this.newsContent.css[0] != null) {
          let _link = document.createElement("link");
          _link.setAttribute("rel", "stylesheet");
          _link.setAttribute("type", "text/css");
          _link.setAttribute("href", _this.newsContent.css[0]);
          _this.linkcss = _link;
          let _headTag = document.getElementsByTagName("head")[0];
          _headTag.appendChild(_link);
        }
        _this.isLoading = false;
        _this.newsContent.body = _this.newsContent.body.replace(/src=\"/g, "src=\"http://read.html5.qq.com/image?src=forum&q=5&r=0&imgflag=7&imageUrl=");
      }, function() {
        _this.isLoading = false;
        _this.isError = true;
      })
    },
    clickBack() {
      let _headTag = document.getElementsByTagName("head")[0];
      _headTag.removeChild(this.linkcss);
      this.$router.go(-1);
    }
  },
  mounted() {
    let _this = this;
    this.requestData.id = this.$route.query.id;
    this.getNewsById(this.$route.query.id);
  },
  updated(){
    let _imgArr = document.images;
    let _divBg = document.getElementsByClassName('dis-bg')[0];
    for(let i of _imgArr){
        if(i.classList.contains('content-image')){
          this.imgArr.push(i);
          i.addEventListener('click',function(){
            i.classList.toggle('active-img');
            if(i.classList.contains('active-img')){
              _divBg.classList.remove('dis-none');
            }
            else{
              _divBg.classList.add('dis-none');
            }

            _divBg.addEventListener('click',function(){
              _divBg.classList.add('dis-none');
              i.classList.remove('active-img');
            });
            // this.showBg = i.classList.contains('active-img');

          })
        }
    }
  }

}



