
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'


const baseList = [{
  url: 'javascript:',
  img: 'https://static.vux.li/demo/1.jpg',
  title: '送你一朵fua'
}, {
  url: 'javascript:',
  img: 'https://static.vux.li/demo/2.jpg',
  title: '送你一辆车'
}, {
  url: 'javascript:',
  img: 'https://static.vux.li/demo/3.jpg',
  title: '送你一次旅行'
}]
export default {
  components: {
    Swiper,
    SwiperItem
  },
  data(){
    return {
      demo01_list:null,
      demo01_index: 0,
    }
  },
  methods:{
    demo01_onIndexChange (index) {
      this.demo01_index = index
    },
    setSwiperData(){
      let _list = [];
      this._props.swiperData.map((val,idx) => {
        let _obj = {
          url:'javascript',
          img:"http://read.html5.qq.com/image?src=forum&q=5&r=0&imgflag=7&imageUrl="+val.image,
          title:val.title,
          id:val.id,
        };
        _list.push(_obj);
      })
      this.demo01_list = _list;
    },
    clickFunc(){
      let _this = this;
      this.demo01_list.map((val,idx)=>{
        if(idx == _this.demo01_index){
          this.$router.push({
            path: 'con',
            query: {
              id: val.id || ""
            }
          });
        }
      })
    }
  },
  props:{
    swiperData:{
      default:baseList,
      type:Array
    }
  },
  mounted(){
    this.setSwiperData();
  }
}



